import React, { useState, useRef, useEffect } from "react";
import { withRouter, Link } from "react-router-dom";
import { connect, useDispatch, useSelector } from "react-redux";
import IntlTelInput from "react-intl-tel-input";
import "react-intl-tel-input/dist/main.css";
import { updateStory } from "../redux/actions/topStoryAction";
import SunEditor from "suneditor-react";
import "suneditor/dist/css/suneditor.min.css";
import { faTimes, faUserEdit } from "@fortawesome/free-solid-svg-icons";

import { basic, complex, formatting } from "./misc/buttonList";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faEye,
  faTrashAlt,
  faTimesCircle,
} from "@fortawesome/free-regular-svg-icons";
import {
  deleteCategory as deleteCategoryAction,
  deleteSubCategory,
} from "../redux/actions/CategoryAction";

import Toast from "light-toast";
import axios from "axios";
import { Row, Col, Container } from "reactstrap";
import { Editor } from "@tinymce/tinymce-react";
import MultipleSelect from "./Common/categorySelector";
import { indigo } from "@material-ui/core/colors";
import API from "../redux/url";


const CategoryUpdate = (props) => {
  console.log(props.match.params.id);
  const id = props.match.params.id;
  let dispatch = useDispatch();

  const [state, setState] = useState({
    id: props.match.params.id,
  });

  const [subcategory, setSubCategory] = useState(null);

  useEffect(() => {
    axios.post(API + "/api/categories/get_one" , {id}).then((result) => {
      setSubCategory([result.data]);
      // setCategories2(result.data);
    });
  }, []);
  console.log(subcategory);

  const [description, setDescription] = useState(null);

  let success = useSelector((state) => state.topStoryReducer.msg);

  useEffect(() => {
    if (success) {
      Toast.success("Successfully Created...!!", 2000, () => {});
    }
  }, [success]);

  const onSubmit = (e, id) => {
    e.preventDefault();
    let obj = {
      ...state,
    };
    if (description) obj.description = description;
    console.log(obj);
    // dispatch(updateStory(obj));
  };

  const onChangeHandler = (e) => {
    if (e.target.name == "image") {
      let val = e.target.files[0];
      setState({ ...state, [e.target.name]: val });
    } else {
      setState({ ...state, [e.target.name]: e.target.value });
    }
  };
  const onChangeHandlers =  (e , key) =>{

    console.log(e.target.value)
    console.log(key)

   let newSubCategory = e.target.value

   subcategory && subcategory.map((item , index)=>{
     return  key == index ? item.sub_cate.push(newSubCategory) : null 
    })

   console.log(subcategory)
  }


  const editorRef = useRef < SunEditor > null;
  useEffect(() => {
    // Get underlining core object here
    // Notice that useEffect is been used because you have to make sure the editor is rendered.
    console.log(editorRef.current?.editor.core);
  }, []);
  const handleChange = (content) => {
    setDescription(content); //Get Content Inside Editor
  };
  const removeSubCate = (id) => {
    dispatch(deleteSubCategory(id));
  };
  return (
    <>
      <div class="content-wrapper">
        <div
          id="order_preview"
          class="wow fadeInUp content_box"
          style={{ visibility: "visible", animationName: "fadeInUp" }}
        >
          <Row className="table-header">
            <Col xs="12" md="12">
              <h2 class="section-title">Update Category</h2>
            </Col>
          </Row>
          <div class="row">
            <Row>
            {
              subcategory && subcategory.map((item , key)=>{
              return  <Col xs="12" md="9">
               
               
                <form>
                  <div class="form-group">
                    <label for="pwd"> Selected Category ({item.category_name})</label>
                    <input
                      type="text"
                      class="form-control"
                      required
                      name="storytitle"
                      onChange={onChangeHandler}
                        // value={item.category_name}
                        // readOnly
                    />
                  </div>

                  {/* <div class="form-group">
                    <label for="pwd">Category</label>
                    <MultipleSelect changeHandler={onChangeHandler} />
                  </div> */}

                
                 
                      <table>
                     <th>Current-Category</th>
 
                     <tr>
                       <td>
                         <td width="200px">
                           {
                           item.sub_cate.length > 0
                             ? item.sub_cate.map((sub, key) => (
                                 <span className="tags_list" key={key}>
                                   {sub.sub_category_name}
                                   <FontAwesomeIcon
                                     icon={faTimes}
                                     className="icon_corss"
                                     title="remove"
                                     onClick={() => removeSubCate(sub._id)}
                                   />
                                 </span>
                               ))
                             : "No Sub-Category Added"}
                         </td>
                       </td>
                     </tr>
                   </table>
          
                   <div class="form-group">
                    <label for="pwd"> Update Sub Category</label>
                    <input
                      type="text"
                      class="form-control"
                      required
                      name="storytitle"
                      onChange={e => onChangeHandlers(e , key)}
                        
                        // readOnly
                    />
                  </div>
                 
                  <div class="form-group">
                    <label for="pwd">Category Descriptions</label>

                    <SunEditor
                      setOptions={{
                        height: 200,
                        buttonList: formatting,
                        buttonList: basic,
                        buttonList: complex,
                      }}
                      onChange={handleChange}
                    />
                  </div>

                  <div class="form-group">
                    <label for="pwd">Upload Image</label>
                    <input
                      type="file"
                      class="form-control"
                      required
                      name="image"
                      onChange={onChangeHandler}
                    />
                  </div>
                  <div class="form-group featuredItems_out">
                    <input
                      type="checkbox"
                      class=""
                      required
                      name="isFeatured"
                      id="featured_item"
                      onChange={onChangeHandler}
                    />
                    <label for="featured_item">Featured</label>
                  </div>
                  <button
                    type="submit"
                    class="btn btn-default"
                    onClick={(e) => onSubmit(e, props.match.params.id)}
                    style={{
                      marginTop: 10,
                      float: "right",
                      padding: "8px 16px",
                      fontSize: 16,
                    }}
                  >
                    Update
                  </button>
                </form>
              </Col>
              })
            }
            </Row>
          </div>
        </div>
      </div>
    </>
  );
};

export default withRouter(CategoryUpdate);
export let successFul, createdFail, getPaymentType, getExpenseGroup;
