import React, { useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faUsers,
  faFile,
  faBullhorn,
  faDollarSign,
  faChartBar,
  faCogs,
  faMoneyBillWaveAlt,
  faPlusCircle,
  faChartLine,
} from "@fortawesome/free-solid-svg-icons";
import { faFileAlt, faListAlt } from "@fortawesome/free-regular-svg-icons";
import { Link } from "react-router-dom";
import Dialog from "@material-ui/core/Dialog";
import MuiDialogContent from "@material-ui/core/DialogContent";
import { logoutuser } from "../redux/actions/authAction";
import { withRouter } from "react-router-dom";
import { useSelector } from "react-redux";

function Home(props) {
  const userInfo = useSelector((state) => state.auth.userInfo);

  const routes = [
    {
      title: "Add Top Story",
      link: "/admin/create_new_top_story",
      icon: faUsers,
    },

    {
      title: "Add Article",
      link: "/admin/create_new_article",
      icon: faUsers,
    },

    {
      title: "Add Categories",
      link: "/admin/CategoriesAddNew",
      icon: faListAlt,
    },
    userInfo && userInfo.user && !userInfo.user.isEditor
      ? {
          title: "Add Editors",
          link: "/admin/add_editor",
          icon: faListAlt,
        }
      : null,
    {
      title: "Add Video Article",
      link: "/admin/add_video",
      icon: faListAlt,
    },
  ];

  return (
    <>
      <div class="welcome_section">
        <div class="row first_row">
          {true ? (
            <>
              {routes.map((item, key) =>
                item ? (
                  <div class="col-md-4 wow flipInY" data-wow-delay="0.1s">
                    <Link to={item.link} class="link ">
                      <div class="box">
                        <div class="col-xs-4 col">
                          <p class="icon color1">
                            <i class="fa fa-users"></i>
                            <FontAwesomeIcon icon={item.icon} />
                          </p>
                        </div>
                        <div class="col-xs-8 col">
                          <h2 class="section-title">{item.title}</h2>
                        </div>
                      </div>
                    </Link>
                  </div>
                ) : null
              )}
            </>
          ) : null}
        </div>
      </div>
    </>
  );
}

export default withRouter(Home);
export let roleChanger;
